import React, { Suspense, useEffect } from 'react';
import { isTagEmbed, isTagbox } from "../../../../actions/api";
const MediaIcon = React.lazy(() => import('../../Elements/MediaIcon/MediaIcon'));
const Image = React.lazy(() => import('../../Elements/Media/Image'));
const Video = React.lazy(() => import('../../Elements/Media/Video'));

const Media = ({ itemData, wallID, ownerId, wall }) => {
  const autoPlayTagEmbed = isTagEmbed && (itemData.type === 3 || itemData.type === 5) && !wall.Personalization.mobilePopup && !wall.Personalization.postFeatured ? true : false
  const autoPlay = (itemData.type === 3 || itemData.type === 5) && wall.ThemeRule.autoPlay == 1 ? true : false
  const size = isTagEmbed || isTagbox ? wall.ThemeRule.aspectImageRatio ? wall.ThemeRule.aspectImageRatio : false : false


  return <div className="tb_cc_media_wrap" role="button" tabIndex="0" aria-label="Pop Up Button">
    {!autoPlayTagEmbed ? 
      <Suspense fallback={<></>}>
        <MediaIcon itemData={itemData} IconClass="tb_cc_media_icon" show={true} isCenter={isTagEmbed} /> 
      </Suspense>
    : null}
    {autoPlay || autoPlayTagEmbed ?
      <Suspense fallback={<></>}>
        <Video VideoClass="tb_cc_video" data={itemData} wallID={wallID} themeID={wall.Personalization.widgetTheme} ownerId={ownerId} size={size} isCover={true} controls={false} autoPlay={isTagbox ? true : false} muted={true} />
      </Suspense>
      :
      <Suspense fallback={<></>}>
        <Image ImageClass="tb_cc_image" data={itemData} wallID={wallID} themeID={wall.Personalization.widgetTheme} ownerId={ownerId} size={size} ThemeRule={wall.ThemeRule} />
      </Suspense>
    }
  </div>
}
export default Media